import styled from 'styled-components';

export const AllLayout = styled.div`
  .substr {
    text-overflow: ellipsis;
    word-break: break-all;
    display: inline-block;
    padding-left: 5px;
    overflow: hidden;
    height:29px; white-space: nowrap;
  }
  .col-span {
    color:red;
  }
  .layout-header{
      margin:15px 0;
      .ant-form-item{
          margin-right:0;
      }
  }
  
  .ant-col.ant-form-item-control-wrapper {
        padding: 0 5px;
    }
    .ant-select.ant-select-enabled {
        margin-top: -2px !important;
            vertical-align: middle;
    }
    .text-color {
        color: red;
    }
    
     .tableStyle .ant-table{
          overflow-x: auto;
      }
    .tableStyle .ant-table-content {
          min-width: 1300px;
          overflow-x: auto;
      }
`;

export const SectionLayout = styled.div`
  .layout-header{
      margin:15px 0;
  }
  
  .search {
      float: right;
      height: 36px;
      line-height: 36px;
  }
`;

