import React, {PureComponent} from "react";
import {Button, DatePicker, Form, Input, Select, Table} from 'antd';
import {DetailRemindStyle} from "./style";
import {disabledEndDate} from '../../../utils';

const remindList = [
    {

        id: '14548511',
        bridgeName: '',
        mileage: 'K10~K20',
        typeName: "30mT",
        name: '1-1',
        nextStateName: '',
        nextBeginDate: '2019/07/08',
        nextStageName: '',
        pedestalName: '1#',
        userName: '',
        remark: ''
    },
    {

        id: '188848511',
        bridgeName: '',
        mileage: 'K10~K20',
        typeName: "30mT",
        name: '1-1',
        nextStateName: '',
        nextBeginDate: '2019/07/08',
        nextStageName: '',
        pedestalName: '1#',
        userName: '',
        remark: ''
    },
]
const EditableContext = React.createContext();

class EditableCell extends React.Component {
    getInput = () => {
        let renderInput = '';
        switch (this.props.dataIndex) {
            case 'nextBeginDate':
                renderInput = <DatePicker format="YYYY-MM-DD" disabledDate={disabledEndDate}/>;

                break;
            case 'pedestalName':
                renderInput = <Select
                    showSearch
                    style={{width: '100%'}}
                    placeholder=""
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                        option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    }
                >
                    {
                        // paramsDisappear.pedestalList && paramsDisappear.pedestalList.length ?
                        //     paramsDisappear.pedestalList.map(item => (
                        //         <Option value={item.k} key={item.v}>{item.v}</Option>
                        //     )) : null
                    }
                </Select>;
                break;

            case 'userName':
                renderInput = <Select
                    showSearch
                    style={{width: '100%'}}
                    placeholder=""
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                        option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    }
                >
                    {
                        // paramsDisappear.userList && paramsDisappear.userList.length ?
                        //     paramsDisappear.userList.map(item => (
                        //         <Option value={item.realName} key={item.userName}>{item.realName}</Option>
                        //     )) : null
                    }
                </Select>
                break;

            case 'remark':
                renderInput = <Input/>
                break;
        }
        return renderInput;
    };

    renderCell = ({getFieldDecorator}) => {
        // console.log(this.props,'');
        const {
            editing,
            dataIndex,
            title,
            inputType,
            record,
            index,
            children,
            ...restProps
        } = this.props;
        console.log('index',index);
        
        return (
            <td {...restProps}>
                {editing ? (
                    <Form.Item style={{margin: 0}}>
                        {getFieldDecorator(dataIndex, {

                            rules: [
                                {
                                    required: true,
                                    message: `${title}!`,
                                },
                            ],
                            // initialValue: record[dataIndex],
                        })(this.getInput())}
                    </Form.Item>
                ) : (
                    children
                )}
            </td>
        );
    };

    render() {
        return <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>;
    }
}

class DetailRemind extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            remindList: remindList,
            selectedRowKeys: []
        };
        this.columns = [
            {
                title: '',
                key: 'id',
                width: 50,
                render: (text, record, index) => index + 1
            },
            {
                title: '',
                dataIndex: 'bridgeName',
                width: 100,
            },
            {
                title: '',
                dataIndex: 'mileage',
                width: 100,
            },
            {
                title: '',
                dataIndex: 'typeName',
                width: 100,
            },
            {
                title: '',
                dataIndex: 'name',
                width: 100,
            },
            {
                title: '',
                dataIndex: 'nextStateName',
                width: 100,
            },
            {
                title: '',
                dataIndex: 'nextBeginDate',
                width: 150,
                editable: true
            },
            {
                title: '',
                dataIndex: 'nextStageName',
                width: 100,
            },
            {
                title: '',
                dataIndex: 'pedestalName',
                width: 150,
                editable: true
            },
            {
                title: '',
                dataIndex: 'userName',
                width: 150,
                editable: true

            },
            {
                title: '',
                dataIndex: 'remark',
                editable: true

            }
        ];
    }
    cancel = () => {
        this.setState({editingKey: ''});
    };

    save(form, key) {
        form.validateFields((error, row) => {
            if (error) {
                return;
            }
            const newData = [...this.state.data];
            const index = newData.findIndex(item => key === item.key);
            if (index > -1) {
                const item = newData[index];
                newData.splice(index, 1, {
                    ...item,
                    ...row,
                });
                this.setState({data: newData, editingKey: ''});
            } else {
                newData.push(row);
                this.setState({data: newData, editingKey: ''});
            }
        });
    }

    // 
    batchEdit() {
        console.log(this.state.selectedRowKeys);
    }

    // 
    handleResize = index => (e, {size}) => {
        this.setState(({columns}) => {
            const nextColumns = [...columns];
            nextColumns[index] = {
                ...nextColumns[index],
                width: size.width,
            };
            return {columns: nextColumns};
        });
    };

    // 
    onSelectChange = (selectedRowKeys, selectedRows, remindList) => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);
        console.log('changed: ', selectedRows);
        this.setState({
            selectedRowKeys,
        });
        remindList.forEach(item => {
            item.isEdit = false
        })
        selectedRows.forEach(item => {
            item.isEdit = true
        })
    };

    render() {
        const components = {
            body: {
                cell: EditableCell,
            },
        };

        const columns = this.columns.map((col, index) => {
            if (!col.editable) {
                return col;
            }
            return {
                ...col,
                onCell: record => ({
                    record,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    editing: record.isEdit,
                }),
                onHeaderCell: column => ({
                    width: column.width,
                    onResize: this.handleResize(index),
                }),
            };
        });

        console.log(columns);


        // 
        const {selectedRowKeys, remindList} = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: (selectedRowKeys, selectedRows) => this.onSelectChange(selectedRowKeys, selectedRows, remindList),
            hideDefaultSelections: true,
        };

        return (
            <DetailRemindStyle>
                <div className='layout-header'>
                    <Button type="primary" onClick={() => {
                        this.batchEdit()
                    }}></Button>
                    <Form
                        layout="inline"
                        style={{
                            float: 'right',
                        }}>
                        {/*<FormItem label="">*/}
                        {/*    <Select*/}
                        {/*        onChange={this.handleChange}*/}
                        {/*        placeholder=""*/}
                        {/*        style={{*/}
                        {/*            width: 200*/}
                        {/*        }}>*/}
                        {/*        <Option value="jack"></Option>*/}
                        {/*        <Option value="lucy"></Option>*/}
                        {/*        <Option value="Yiminghe">()</Option>*/}
                        {/*    </Select>*/}
                        {/*</FormItem>*/}
                    </Form>
                </div>

                <EditableContext.Provider value={this.props.form}>
                    <Table
                        rowSelection={rowSelection}
                        rowKey="id"
                        components={components}
                        bordered
                        dataSource={remindList}
                        columns={columns}
                        // rowClassName="editable-row"
                        pagination={false}
                        scroll={{y: 600}}
                    />
                </EditableContext.Provider>
            </DetailRemindStyle>

        );
    }

    componentDidMount() {
        const type = 1;

        this.getRemindList(type)
    }

    getRemindList(type) {

    }
}

export default Form.create()(DetailRemind)